.TBmainPost { 
    width: 750px;
    height: 850px;
    overflow: hidden;;
    margin: 0 0 20px 0;
    padding: 0;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    border: 2px solid rgb(235, 235, 235);
    box-shadow: rgba(104, 104, 104, 0.2) 0px 2px 8px 0px;
    box-sizing: border-box;
    position: relative;
    border-radius: 3px;
}
.TBpostImage {
    width: 680px;
    height: 540px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 3px;
}
.TBmainPost p { 
    text-align: left;
    max-height: 100px;
    line-height: 1.5rem;
}
.TBmainPost h3 { 
    text-align: left;
    font-size: 1.6rem;
}
.TBwhiteBottom {
    position: absolute;
    bottom: -1px;
    background: rgb(2,0,36);
    background: linear-gradient(180deg, rgba(2,0,36,0) 0%, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 41%);
    width:770px;
    height: 120px;
    margin-left: -19px;
    padding-top: 20px;  
}
.TBreadMoreButton {
    margin: 40px 620px 0 0 ;
    height: 40px;
    width: 130px;
    font-size: 1rem;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-weight: 700;
}